@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
.header>div{padding:0;width:1170px;margin:0 auto}.header .logo_wrapper{flex-grow:1}.header .logo{width:156px;height:62px;display:flex;align-items:center}.header_menu{padding:0;margin:0;display:flex;list-style:none}.header_menu li a{color:#333;text-decoration:none;padding:10px 20px;border-radius:4px}.header_menu li a:hover{text-decoration:none;background-color:rgba(0,0,0,0.08)}.header_menu li:last-of-type{margin-right:0}.header_menu li.active a{background-color:rgba(0,0,0,0.08)}.header .top_menu_wrapper a.active{background-color:rgba(0,0,0,0.04)}.header .top_menu_wrapper a span{text-transform:capitalize}

.profile_page_wrapper .main_content{background:center/cover no-repeat url(/static/media/bg.471e69aa.jpg)}

.mapbox_container{position:absolute;top:0;left:0;width:100%;height:1000px}.marker{display:block;left:0;top:0;position:absolute;background-size:contain}.marker.start{background-image:url(data:image/png;base64,);width:1.2rem;height:1.2rem}.marker.end{width:4.8rem;height:4.8rem;background-image:url(data:image/png;base64,)}

.is_booked_message h1{margin-bottom:32px}.is_booked_message p{margin-bottom:70px}

.order_form_container{padding:45px;width:487px;height:317px;box-sizing:border-box}.order_form_container .inputs_area{margin-bottom:30px}

.empty_profile_message{padding:46px 51px}.empty_profile_message h1{margin-bottom:32px}.empty_profile_message p{margin-bottom:20px}

.map_page_wrapper .main_content{position:relative}.map_page_wrapper .order_form_wrapper{width:auto;position:absolute;top:80px}

.form_wrapper{padding:62px 73px 72px}.form_wrapper .form_profile_header{margin-bottom:50px}.form_wrapper .form_profile_header h1{font-weight:bold}.form_wrapper .form_profile_input:hover:not(.Mui-disabled):before{border-bottom-color:#eaeaea}.form_wrapper .form_profile_input:after{border-bottom-color:#eaeaea}.form_wrapper .form_profile_input.Mui-focused:after{border-bottom-color:#ffb1b1}.form_wrapper .form_profile_input.Mui-focused input{font-weight:400}.form_wrapper .form_profile_input:before{border-bottom-color:#eaeaea}.form_wrapper .form_profile_input input{font-size:24px;font-weight:300}.form_wrapper .form_profile_input input#cardName{text-transform:uppercase}.form_wrapper .form_profile_label{display:flex;align-items:center;font-weight:400;z-index:1}.form_wrapper .form_profile_label.Mui-focused{color:#747474}.form_wrapper .form_profile .MuiInputBase-input{font-size:24px;font-weight:300}.form_wrapper .form_profile .show_password_trigger{opacity:.5}.form_wrapper .card{width:319px;height:162px;position:relative;padding:30px;box-shadow:0 0 26px rgba(0,0,0,0.05),0 15px 10px rgba(0,0,0,0.06)}.form_wrapper .card.bg_icon{background:top 18px right 18px/39px 24px no-repeat url(/static/media/mastercard.16cdbb0c.png)}.form_wrapper .card_col{height:100%}.form_wrapper .cards_row{margin-bottom:47px}.form_wrapper .form_footer button:first-of-type{margin-left:-10px}.form_wrapper .form_footer button:last-of-type{margin-left:10px;box-shadow:none}.form_wrapper .form_footer button:last-of-type:hover{background-color:#d8d8d8}

.profile_page_wrapper .paper_container{width:946px}

.logout_wrapper .form{padding:65px 53px}.logout_wrapper .form h1{margin-bottom:22px;line-height:1.6rem}.logout_wrapper .form_control{margin-top:39px}.logout_wrapper .form .btn{padding:7px 49px;font-size:21px}.logout_wrapper .form_footer{margin-top:40px}.logout_wrapper .form p a{color:#1473e6;text-decoration:none;font-weight:500}

.logout_wrapper .main_content{background:center/cover no-repeat url(/static/media/bg.471e69aa.jpg)}.logout_wrapper .main_content .content .logo{width:157px;height:37px}@media all and (min-width: 1170px){.logout_wrapper .main_content .content .paper_container{width:517px}}

.main_wrapper{min-height:100vh;display:flex;flex-direction:column}.main_wrapper .main_content{flex-grow:1;display:flex;justify-content:center;align-items:center}.main_wrapper .main_content .content{display:flex;align-items:center;justify-content:center;margin-top:-70px}.main_wrapper .mapboxgl-ctrl.mapboxgl-ctrl-attrib{display:none}.main_wrapper .btn:hover{background-color:#eeb60c}.main_wrapper .overlay{position:relative;z-index:10}.main_wrapper .icon_wrapper{display:flex}.main_wrapper .icon_wrapper.pointer{cursor:pointer}.main_wrapper form label{color:#747474}@media all and (min-width: 1170px){#app .wrapper{width:1170px;margin:0 auto}}.popperArrow{content:"";position:absolute;top:-8px;left:99px;height:12px;width:12px;background:inherit;transform:rotate(45deg);border-top:inherit;border-left:inherit;box-shadow:inherit}.validation_error{font-size:10px;color:red;position:absolute;bottom:-15px}.date_picker{display:none}

body{margin:0;font-family:Roboto, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

